          text-align: center;
          align-items: center;
          margin: 20px 135px 0 0;
  
          img {
            width: 150%;
            height: auto;
          }
        }
  
        .btn {
          margin: 30px;
          cursor: pointer;
          font-size: 14pxm;
          font-weight: bold;
          color: orangered;
          border: 1px solid orangered;
          border-radius: 45px;
          padding: 0.5em 1.5em;
          margin: 32px auto0;
          min-width: 4em;
          box-sizing: content-box;
          display: inline-block;
        }
      }
  
