.coffee-content {
    max-width: 1320px;
    padding: 0 20px;
    position: relative;
    width: 100%;
    margin: 0 auto;


    article {
        text-align: left;
        margin-bottom: 5rem;
        display: block;
        box-sizing: border-box;

        & h2{
            font-size: 2.7rem;
            margin: 0;
            font-weight: normal;
            line-height: 1.2em;
        }

        & ul{
            display: flex;
            flex-wrap: wrap;
            align-content: flex-start;
            list-style: none;

            & li{
                width: calc(100%/5 - 2.2rem);
                margin: 1.5rem 2.5rem 1.5rem 0;

                
            }
        }
    }
}